.intro {
  width: 100vw;
  height: 100vh;
  &__inner {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  &__circle {
    position: absolute;
    content: "";
    right: 20%;
    bottom: 28%;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    transform: scale(1.5);
    box-shadow: 0 0 100px 20px rgb(255, 255, 255);
  }
  &__text {
    font-size: 60px;
    position: absolute;
    z-index: 2;
    right: 20%;
    bottom: 45%;
    color: white;
    letter-spacing: 10px;
    font-family: var(--xtrabold-ff);
    text-shadow: 0 0 10px 10px;
  }
}
