.nav {
  position: fixed;
  left: 30px;
  top: 50%;
  transform: translateY(-50%);
  z-index: 2;
  display: flex;
  flex-direction: column;
  gap: 25px;
  &__button {
    width: 30px;
    height: 3px;
    background: var(--primary);
    transition: width 0.2s;
  }
}

.current {
  width: 50px;
}
